<!-- 视频主页面 -->
<template>
    <el-row>
        <el-col :span="24" v-for='(videoOne, outIndex) in form' :key="outIndex">
            <div v-for='(video, inIndex) in videoOne' :key="inIndex" :span="24">
                <h1 v-if="inIndex == 0" :span="4" style="text-align: center;">{{ video.name }}</h1>
                <h4 v-if="inIndex == 0">{{ video.text }}</h4>
                <el-col :span="6" v-if="inIndex != 0">
                    <el-card :body-style="{ padding: '20px' }" class="el-card">
                        <el-image :src=video.imgurl class="image" @click="push(video)"></el-image>
                        <div style="padding: 14px;">
                            <span>{{ video.collection }}</span><br>
                        </div>
                    </el-card>
                </el-col>
            </div>
        </el-col>
    </el-row>
</template>

<script>
import axios from 'axios';
const options = {
    data() {
        return {
            form: {

            }
        }
    },
    methods: {
        push(video) {
            this.$store.commit('saveVideo', video);
            //跳转到VideoOne界面
            this.$router.push('/VideoOne')
        },
        selectVideo() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/video/select",
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    this.form = res.data;
                });
        }
    },
    mounted: function () {
        this.selectVideo();
    }
}
export default options;
</script>

<style>
.image {
    width: 100%;
    display: block;
    height: 200px;
}

.el-card {
    width: 100%;
}
</style>